<template lang="html">
  <div class="">

    <div class="columns">
    <div class="column is-3 is-offset-2">
      <div class="field">
        <label class="label">user input phone</label>
        <div class="control">
          <input class="input" type="text" placeholder="+8801751255157" v-model="phn">
        </div>
      </div>
      <div class="field">
        <label class="label">suggested phn</label>
        <div class="control">
          <input class="input " type="text" :placeholder="status.suggest" disabled>
        </div>
      </div>
      </div>
      <div class="colum is-4 is-offset-2">
        <h1>.</h1>
        <h1>.</h1>
        <table class="table is-bordered is-striped is-narrow is-fullwidth">
  <tbody>
    <tr v-if="status.digit_status">
      <th v-if="status.digit_status" >digit_status =></th>
      <th>{{status.digit_status}}</th>
    </tr>

  <tr v-if="status.need">
    <th v-if="status.need" >need digit =></th>
    <th>{{status.need}}</th>
  </tr>

  <tr v-if="status.suggest">
    <th v-if="status.suggest">suggest phn =></th>
    <th>{{status.suggest}}</th>
  </tr>
  <tr v-if="status.operator_status">
    <th v-if="status.operator_status" >operator_status =></th>
    <th>{{status.operator_status}}</th>
  </tr>
  <tr v-if="status.operator !== undefined">
    <th>operator =></th>
    <th>{{status.operator}}</th>
  </tr>




  </tbody>
</table>



</div>

    </div>
      <hr>
      {{ status }}

      <hr>
      <a class="button is-primary" href="https://github.com/code4mk/bd-phone-validator/blob/master/README.md" target="
      _blank">read doc</a>
       <a class="button is-link" href="https://code4mk.org" target="_blank">code4mk.org</a>

  </div>


</template>

<script>
import bdPhone from '@0devco/bd-phone-validator'
export default {
  data () {
    return {
      msg: 'Atom_Vue',
      phn: '',
      status: []
    }
  },
   mounted() {
    const self = this
    let mm = ''
    var ttt = ''
    window.addEventListener("keyup", function(e) {
      // use self instead of this in here
      let to= self.phn
      self.status = bdPhone(to)
      console.log(to)

    });
  },
  methods: {
    countUp: function (event) {
      // this.r = bdPhone(this.phn)
      // console.log('kk')
      // console.log(this.r)
      this.msg = event
    }
  }
}
</script>

<style lang="css" scoped >


</style>



